// Toast notification store
import { writable } from 'svelte/store';

// Browser detection
const browser = typeof window !== 'undefined';

export interface Toast {
  id: string;
  type: 'success' | 'error' | 'warning' | 'info';
  title?: string;
  message: string;
  duration?: number;
  dismissible?: boolean;
  action?: {
    label: string;
    handler: () => void;
  };
  timestamp: number;
}

// Toast store
const { subscribe, update } = writable<Toast[]>([]);

// Auto-remove timeout tracking
const timeouts = new Map<string, NodeJS.Timeout>();

export const toasts = {
  subscribe,
  
  // Add a new toast
  add: (toast: Omit<Toast, 'id' | 'timestamp'>) => {
    const id = crypto?.randomUUID() || Math.random().toString(36).substring(2);
    const newToast: Toast = {
      id,
      timestamp: Date.now(),
      duration: 5000, // Default 5 seconds
      dismissible: true,
      ...toast
    };
    
    update(toasts => [...toasts, newToast]);
    
    // Auto-dismiss after duration
    if (newToast.duration && newToast.duration > 0) {
      const timeout = setTimeout(() => {
        toasts.dismiss(id);
      }, newToast.duration);
      
      timeouts.set(id, timeout);
    }
    
    return id;
  },
  
  // Dismiss a specific toast
  dismiss: (id: string) => {
    update(toasts => toasts.filter(toast => toast.id !== id));
    
    // Clear timeout if exists
    const timeout = timeouts.get(id);
    if (timeout) {
      clearTimeout(timeout);
      timeouts.delete(id);
    }
  },
  
  // Clear all toasts
  clear: () => {
    update(() => []);
    
    // Clear all timeouts
    timeouts.forEach(timeout => clearTimeout(timeout));
    timeouts.clear();
  },
  
  // Convenience methods
  success: (message: string, options?: Partial<Toast>) => {
    return toasts.add({
      type: 'success',
      message,
      ...options
    });
  },
  
  error: (message: string, options?: Partial<Toast>) => {
    return toasts.add({
      type: 'error',
      message,
      duration: 8000, // Errors stay longer
      ...options
    });
  },
  
  warning: (message: string, options?: Partial<Toast>) => {
    return toasts.add({
      type: 'warning',
      message,
      duration: 6000,
      ...options
    });
  },
  
  info: (message: string, options?: Partial<Toast>) => {
    return toasts.add({
      type: 'info',
      message,
      ...options
    });
  },
  
  // Promise helper - shows loading, then success/error
  promise: async <T>(
    promise: Promise<T>,
    options: {
      loading: string;
      success: string | ((data: T) => string);
      error: string | ((error: any) => string);
    }
  ): Promise<T> => {
    const loadingId = toasts.add({
      type: 'info',
      message: options.loading,
      duration: 0, // Don't auto-dismiss
      dismissible: false
    });
    
    try {
      const result = await promise;
      toasts.dismiss(loadingId);
      
      const successMessage = typeof options.success === 'function' 
        ? options.success(result) 
        : options.success;
      
      toasts.success(successMessage);
      return result;
    } catch (error) {
      toasts.dismiss(loadingId);
      
      const errorMessage = typeof options.error === 'function' 
        ? options.error(error) 
        : options.error;
      
      toasts.error(errorMessage);
      throw error;
    }
  }
};

// Persist toasts to session storage (optional)
if (browser) {
  // Load persisted toasts on page load
  try {
    const stored = sessionStorage.getItem('toasts');
    if (stored) {
      const persistedToasts = JSON.parse(stored) as Toast[];
      // Only restore recent toasts (less than 1 minute old)
      const recent = persistedToasts.filter(
        toast => Date.now() - toast.timestamp < 60000
      );
      
      if (recent.length > 0) {
        update(() => recent);
      }
    }
  } catch (e) {
    console.warn('Failed to load persisted toasts:', e);
  }
  
  // Persist toasts on change
  subscribe(toasts => {
    try {
      sessionStorage.setItem('toasts', JSON.stringify(toasts));
    } catch (e) {
      console.warn('Failed to persist toasts:', e);
    }
  });
}