// Theme management store with dark/light mode toggle
import { writable, derived } from 'svelte/store';

// Browser detection
const browser = typeof window !== 'undefined';

export type Theme = 'light' | 'dark';

// Theme preference store
const createThemeStore = () => {
  // Initialize with system preference or default to light
  const getInitialTheme = (): Theme => {
    if (!browser) return 'light';
    
    // Check localStorage first
    const stored = localStorage.getItem('theme') as Theme;
    if (stored && (stored === 'light' || stored === 'dark')) {
      return stored;
    }
    
    // Fall back to system preference
    if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
      return 'dark';
    }
    
    return 'light';
  };

  const { subscribe, set, update } = writable<Theme>(getInitialTheme());

  return {
    subscribe,
    
    // Toggle between light and dark themes
    toggle: () => {
      update(theme => {
        const newTheme = theme === 'light' ? 'dark' : 'light';
        
        if (browser) {
          // Save to localStorage
          localStorage.setItem('theme', newTheme);
          
          // Apply to document
          document.documentElement.setAttribute('data-theme', newTheme);
          
          // Update class for Tailwind dark mode
          if (newTheme === 'dark') {
            document.documentElement.classList.add('dark');
          } else {
            document.documentElement.classList.remove('dark');
          }
        }
        
        return newTheme;
      });
    },
    
    // Set specific theme
    setTheme: (newTheme: Theme) => {
      set(newTheme);
      
      if (browser) {
        localStorage.setItem('theme', newTheme);
        document.documentElement.setAttribute('data-theme', newTheme);
        
        if (newTheme === 'dark') {
          document.documentElement.classList.add('dark');
        } else {
          document.documentElement.classList.remove('dark');
        }
      }
    },
    
    // Initialize theme on app start
    init: () => {
      if (!browser) return;
      
      const current = getInitialTheme();
      set(current);
      
      // Apply initial theme
      document.documentElement.setAttribute('data-theme', current);
      if (current === 'dark') {
        document.documentElement.classList.add('dark');
      } else {
        document.documentElement.classList.remove('dark');
      }
      
      // Listen for system theme changes
      const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
      const handleChange = (e: MediaQueryListEvent) => {
        // Only auto-switch if user hasn't manually set a preference
        const stored = localStorage.getItem('theme');
        if (!stored) {
          const newTheme = e.matches ? 'dark' : 'light';
          set(newTheme);
          document.documentElement.setAttribute('data-theme', newTheme);
          
          if (newTheme === 'dark') {
            document.documentElement.classList.add('dark');
          } else {
            document.documentElement.classList.remove('dark');
          }
        }
      };
      
      if (mediaQuery.addEventListener) {
        mediaQuery.addEventListener('change', handleChange);
      } else {
        // Fallback for older browsers
        mediaQuery.addListener(handleChange);
      }
    }
  };
};

export const theme = createThemeStore();

// Derived stores for convenience
export const isDark = derived(theme, $theme => $theme === 'dark');
export const isLight = derived(theme, $theme => $theme === 'light');

// Theme-aware utility functions
export const getThemeColors = (currentTheme: Theme) => ({
  primary: currentTheme === 'dark' ? '#8b93ff' : '#667eea',
  secondary: currentTheme === 'dark' ? '#9a7bb8' : '#764ba2',
  accent: currentTheme === 'dark' ? '#fbbf24' : '#f59e0b',
  neutral: currentTheme === 'dark' ? '#2a2e37' : '#374151',
  base: currentTheme === 'dark' ? '#1f2937' : '#ffffff',
  content: currentTheme === 'dark' ? '#f9fafb' : '#1f2937',
});

// Animation preferences
export const prefersReducedMotion = writable(false);

if (browser) {
  const mediaQuery = window.matchMedia('(prefers-reduced-motion: reduce)');
  prefersReducedMotion.set(mediaQuery.matches);
  
  const handleMotionChange = (e: MediaQueryListEvent) => {
    prefersReducedMotion.set(e.matches);
  };
  
  if (mediaQuery.addEventListener) {
    mediaQuery.addEventListener('change', handleMotionChange);
  } else {
    mediaQuery.addListener(handleMotionChange);
  }
}