<script lang="ts">
  import { toasts, type Toast } from '$lib/stores/toast';
  import { fly, scale } from 'svelte/transition';
  import { CheckCircle, XCircle, AlertTriangle, Info, X } from 'lucide-svelte';
  
  export let position: 'top-right' | 'top-left' | 'bottom-right' | 'bottom-left' | 'top-center' = 'top-right';
  export let maxToasts = 5;
  
  // Limit visible toasts
  $: visibleToasts = $toasts.slice(-maxToasts);
  
  const positionClasses = {
    'top-right': 'top-4 right-4',
    'top-left': 'top-4 left-4',
    'bottom-right': 'bottom-4 right-4',
    'bottom-left': 'bottom-4 left-4',
    'top-center': 'top-4 left-1/2 transform -translate-x-1/2'
  };
  
  function getIcon(type: Toast['type']) {
    switch (type) {
      case 'success': return CheckCircle;
      case 'error': return XCircle;
      case 'warning': return AlertTriangle;
      case 'info': return Info;
      default: return Info;
    }
  }
  
  function getColors(type: Toast['type']) {
    switch (type) {
      case 'success': 
        return {
          bg: 'bg-success/10 border-success/20',
          icon: 'text-success',
          title: 'text-success-content',
          message: 'text-base-content/80'
        };
      case 'error': 
        return {
          bg: 'bg-error/10 border-error/20',
          icon: 'text-error',
          title: 'text-error-content',
          message: 'text-base-content/80'
        };
      case 'warning': 
        return {
          bg: 'bg-warning/10 border-warning/20',
          icon: 'text-warning',
          title: 'text-warning-content',
          message: 'text-base-content/80'
        };
      case 'info': 
        return {
          bg: 'bg-info/10 border-info/20',
          icon: 'text-info',
          title: 'text-info-content',
          message: 'text-base-content/80'
        };
      default: 
        return {
          bg: 'bg-base-200 border-base-300',
          icon: 'text-base-content',
          title: 'text-base-content',
          message: 'text-base-content/80'
        };
    }
  }
  
  function handleDismiss(id: string) {
    toasts.dismiss(id);
  }
  
  function handleAction(toast: Toast) {
    if (toast.action) {
      toast.action.handler();
      toasts.dismiss(toast.id);
    }
  }
</script>

<!-- Toast container -->
<div class="fixed {positionClasses[position]} z-50 space-y-2 max-w-sm w-full pointer-events-none">
  {#each visibleToasts as toast (toast.id)}
    {@const colors = getColors(toast.type)}
    {@const Icon = getIcon(toast.type)}
    
    <div
      class="toast-item pointer-events-auto"
      in:fly={{
        x: position.includes('right') ? 300 : position.includes('left') ? -300 : 0,
        y: position.includes('top') ? -100 : position.includes('bottom') ? 100 : 0,
        duration: 300
      }}
      out:scale={{ duration: 200, start: 0.95 }}
    >
      <div class="relative overflow-hidden rounded-2xl {colors.bg} border backdrop-blur-md shadow-soft">
        <!-- Main content -->
        <div class="p-4">
          <div class="flex items-start space-x-3">
            <!-- Icon -->
            <div class="flex-shrink-0 mt-0.5">
              <Icon class="w-5 h-5 {colors.icon}" />
            </div>
            
            <!-- Content -->
            <div class="flex-1 min-w-0">
              {#if toast.title}
                <h4 class="text-sm font-semibold {colors.title} mb-1">
                  {toast.title}
                </h4>
              {/if}
              
              <p class="text-sm {colors.message}">
                {toast.message}
              </p>
              
              <!-- Action button -->
              {#if toast.action}
                <button
                  class="mt-2 text-xs font-medium {colors.icon} hover:underline focus:outline-none focus:underline"
                  on:click={() => handleAction(toast)}
                >
                  {toast.action.label}
                </button>
              {/if}
            </div>
            
            <!-- Dismiss button -->
            {#if toast.dismissible}
              <button
                class="flex-shrink-0 p-1 rounded-lg hover:bg-base-content/10 transition-colors focus:outline-none focus:ring-2 focus:ring-primary/50"
                on:click={() => handleDismiss(toast.id)}
                aria-label="Dismiss notification"
              >
                <X class="w-4 h-4 text-base-content/60" />
              </button>
            {/if}
          </div>
        </div>
        
        <!-- Progress bar for auto-dismiss -->
        {#if toast.duration && toast.duration > 0}
          <div class="absolute bottom-0 left-0 right-0 h-1 bg-base-content/10">
            <div 
              class="h-full bg-gradient-to-r from-primary-400 to-primary-600 origin-left animate-progress"
              style="animation-duration: {toast.duration}ms"
            ></div>
          </div>
        {/if}
        
        <!-- Subtle glow effect -->
        <div class="absolute inset-0 rounded-2xl bg-gradient-to-r from-transparent via-white/5 to-transparent opacity-50 pointer-events-none"></div>
      </div>
    </div>
  {/each}
</div>

<style>
  .toast-item {
    transform-origin: center;
  }
  
  @keyframes progress {
    from {
      transform: scaleX(1);
    }
    to {
      transform: scaleX(0);
    }
  }
  
  .animate-progress {
    animation: progress linear forwards;
    transform-origin: left;
  }
  
  /* Stacking animation */
  .toast-item:not(:last-child) {
    transform: translateY(0) scale(1);
    transition: transform 0.3s ease;
  }
  
  .toast-item:nth-last-child(2) {
    transform: translateY(-2px) scale(0.98);
  }
  
  .toast-item:nth-last-child(3) {
    transform: translateY(-4px) scale(0.96);
  }
  
  .toast-item:nth-last-child(4) {
    transform: translateY(-6px) scale(0.94);
  }
  
  .toast-item:nth-last-child(n+5) {
    transform: translateY(-8px) scale(0.92);
    opacity: 0.7;
  }
</style>